import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, message } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { TableData } from 'components/common';
import { Btn } from 'components/common/button';
import {
  getAdmissionReceipts,
  cancelReceipt,
  exportExcel,
  exportReportTuitionCredit,
} from 'services/admissionReceipts';
import { moneyToString } from 'utils/formatUtil';
import { getUserInfo } from 'utils/userUtils';
import moment from 'moment';
import SearchBar from './SearchBar';

class AdmissionReceipts extends Component {
  state = {
    paramSearch: {},
    countReceipts: 0,
    totalAmount: 0,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Ký hiệu',
      dataIndex: 'invoiceCode',
      key: 'invoiceCode',
    },
    {
      title: 'Số phiếu',
      dataIndex: 'invoiceNo',
      key: 'invoiceNo',
    },
    {
      title: 'Ngày thu',
      dataIndex: 'paymentDate',
      key: 'paymentDate',
      render: text => moment(text).format('DD-MM-YYYY'),
    },
    {
      title: 'Mã SV',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Số tiền',
      dataIndex: 'amount',
      render: text => moneyToString(text),
      key: 'amount',
    },
    {
      title: 'Nội dung',
      dataIndex: 'paymentContent',
      key: 'paymentContent',
    },
    {
      title: 'Người thu',
      dataIndex: 'casherName',
      key: 'casherName',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => {
        const { isCancelled } = this.state.paramSearch;
        return (
          <Fragment>
            <Btn onClick={() => this.viewReceipt(record.receiptId)}>Xem phiếu</Btn>
            {!isCancelled && (
              <Btn onClick={() => this.cancelReceiptCustom(record.receiptId)}>Hủy phiếu</Btn>
            )}
          </Fragment>
        );
      },
    },
  ];

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    paramSearch.casherId = getUserInfo().id;
    this.setState({ paramSearch });
  };

  handleSearch = async paramSearch => {
    const res = await getAdmissionReceipts(paramSearch);
    const { items } = res.data;
    const totalAmount = items.map(item => item.amount).reduce((a, b) => a + b, 0);
    const countReceipts = items.length;
    this.setState({
      totalAmount,
      countReceipts,
    });
    return res;
  };

  viewReceipt = async receiptId => {
    try {
      await exportReportTuitionCredit(receiptId);
      message.success(`Xem phiếu thành công`);
    } catch (ex) {
      message.error(`Xem phiếu không thành công`);
    }
  };

  cancelReceiptCustom = async receiptId => {
    try {
      await cancelReceipt(receiptId);
      message.success(`Hủy phiếu thành công`);
    } catch (ex) {
      message.error(`Hủy phiếu không thành công`);
    }
  };

  handleExportExcel = () => {
    const paramSearch = getQueryParams();
    if (!paramSearch.receiptConfigId) return;
    exportExcel(paramSearch);
  };

  render() {
    const { paramSearch, countReceipts, totalAmount } = this.state;
    const { receiptConfigId } = paramSearch;
    return (
      <Card>
        <Row>
          <Col span={24}>
            <SearchBar
              countReceipts={countReceipts}
              totalAmount={totalAmount}
              handleExportExcel={this.handleExportExcel}
            />
          </Col>
          <Col span={24}>
            <TableData
              columns={this.columns}
              paramSearch={this.state.paramSearch}
              service={this.handleSearch}
              allowGetData={!!receiptConfigId}
              rowKey="receiptId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AdmissionReceipts />
  </Page>
);



