import { request } from 'utils';

const prefix = '/api/finance';

export const getFeeObjects = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/FeeObject',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



